import $ from 'jquery';

export function upload(imagedata, callback){
    let body = JSON.stringify({ image: imagedata })
    $.ajax({
        url: 'http://127.0.0.1:8000',
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
        },
        body: {body},
        success: function(response){
            callback(response.pred)
        },
        error: function(error){
            console.log(error)
        }
    })
}


